import { Container, Badge } from "react-bootstrap";
import "./Button.scss";
import "./Detail.css";
import { GoogleOAuthProvider } from "@react-oauth/google";
import { useSelector, useDispatch } from "react-redux";
import { GoogleLogin } from "@react-oauth/google";
import OrderCard from "./OrderCard";
import { clearOrders } from "./store";
import axios from "axios";
import { Helmet } from "react-helmet-async";

function Login() {
  const userDetail = JSON.parse(sessionStorage.getItem("userDetail") || null);
  let dispatch = useDispatch();
  let orderState = useSelector((state) => state.order);

  const handleLogOut = () => {
    sessionStorage.removeItem("userDetail");
    window.location.href = "/";
  };

  const handleGoogleLogin = async (res) => {
    try {
      const response = await axios.post("/login", { accessToken: res.credential });

      if (response.data.name && response.data.picture) {
        const userDetail = {
          name: response.data.name,
          picture: response.data.picture,
        };
        sessionStorage.setItem("userDetail", JSON.stringify(userDetail));
        window.location.href = "/";
      } else {
        console.log("Invalid response from server");
      }
    } catch (error) {
      console.log("Login Failed", error);
    }
  };

  return (
    <>
      <Helmet>
        <title>Hochony Login</title>
        <link rel="canonical" href="https://hochony.com/login" />
      </Helmet>
      {userDetail ? (
        <>
          <div className="product-box">
            {orderState.length !== 0 ? (
              <div className="center">
                <img alt="Íµ¨Îß§ÎÇ¥Ïó≠" src={userDetail.picture} style={{ width: "30px", marginRight: "15px" }} />
                <h5 style={{ margin: 0 }}>{userDetail.name}ÎãòÏùò Íµ¨Îß§ÎÇ¥Ïó≠Ïù¥ÏóêÏöî!</h5>
              </div>
            ) : (
              <div className="center">
                <img alt="Îπà Íµ¨Îß§ÎÇ¥Ïó≠" src={userDetail.picture} style={{ width: "30px", marginRight: "15px" }} />
                <h5 style={{ margin: 0 }}>{userDetail.name}ÎãòÏùò Íµ¨Îß§ÎÇ¥Ïó≠Ïù¥ ÎπÑÏóàÏñ¥Ïöî! </h5>
              </div>
            )}
          </div>

          <div className="container">
            <div className="row">
              {orderState.map((order, i) => {
                return <OrderCard order={order} key={i} />;
              })}
            </div>
          </div>
        </>
      ) : null}

      <Container className="col-lg-4">
        <img alt="Í±∞Îßå Ìò∏Ï≤ú" src={require("./assets/hochonylogin.webp")} className="product-img mt-3" width="500" height="500" />
        <div className="product-box">{userDetail ? <h6>Ïñ¥Ïù¥ {userDetail.name}, Í∞ÄÎäî Í±∞ÎÉê.</h6> : <h6>Ïñ¥Ïù¥, Î°úÍ∑∏Ïù∏Ïù¥ÎÇò Ìï¥Îùº.</h6>}</div>
        {userDetail ? (
          <>
            <button className="buttonPink mb-5" role="button" aria-label="buttonLogOut" onClick={handleLogOut}>
              Î°úÍ∑∏ÏïÑÏõÉ
            </button>
            <button className="buttonGray mb-5" role="button" aria-label="buttonDelete" onClick={() => dispatch(clearOrders(orderState))}>
              ÎÇ¥Ïó≠ÏÇ≠Ï†ú
            </button>
          </>
        ) : (
          <>
            <div className="googleText">
              <Badge pill bg="light" text="dark">
                Íµ¨Í∏Ä Î°úÍ∑∏Ïù∏ ÌïòÍ≥† ÎçîÎ≥¥Í∏∞!
              </Badge>
            </div>
            <div className="googleBox">
              <GoogleOAuthProvider clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID}>
                <GoogleLogin text="signin_with" shape="pill" onSuccess={handleGoogleLogin} />
              </GoogleOAuthProvider>
            </div>
          </>
        )}
      </Container>
    </>
  );
}

export default Login;
